import {useEffect} from "react"
import {Provider} from "react-redux"
import {Routes, Route, HashRouter} from "react-router-dom"
import {App} from "@capacitor/app"

import {TOC} from "../components/toc"
import Stats from "../components/stats"
import Chapter from "../components/chapter"
import {bookStore} from "../state/book-reducer"
import {Layout} from "./layout"

export default function ChapterRouter() {
    useEffect(() => {
        // this is for Android back button, no problem for browser web
        console.log("adding listener")
        App.addListener('backButton', ({canGoBack})=>{
            console.log("back button clicked")
            if(canGoBack) {
                window.history.back()
            }else {
                App.exitApp()
            }
        })
    }, []);

    return <HashRouter history={history}>
        <Provider store={bookStore}>
            <Routes>
                <Route path="/" element={<Layout/>}>
                    <Route index element={<TOC/>}/>
                    <Route path="/stats" element={<Stats/>}/>
                    {/* 18 Routes below */}
                    {
                        Array(18).fill(1)
                            .map((dummy, i) => i + 1)
                            .map(chapNum =>
                                <Route key={chapNum} path={`/${chapNum}`} element={<Chapter chapterNum={chapNum}/>}/>
                            )
                    }
                </Route>
                <Route path="*" element={<Layout/>}/>
            </Routes>
        </Provider>
    </HashRouter>
}