import React, {useState} from 'react'
import {useDispatch, useSelector} from "react-redux"
import {FloatButton, Select} from "antd"
import {SyncOutlined} from "@ant-design/icons"

import {bookActions} from "../state/book-reducer"

export function FloatingMenu({count}) {
    let [expand, setExpand] = useState(false)
    let bookState = useSelector(state => state.book)
    let dispatch = useDispatch()

    const getArray = length => {
        return Array(length).fill(1).map((item, index) => ({value: index + 1, label: index + 1}))
    }

    let repeatString = (() => {
        let repeatString = null
        if (bookState.repeat.oneToggle) {
            repeatString = 1
        } else if (bookState.repeat.manyToggle) {
            repeatString = bookState.repeat.manyToggle ? `${bookState.repeat.fromSloka} - ${bookState.repeat.toSloka}` : ""
        }
        return !expand ? repeatString : ""
    })()
    return <>
        {expand &&
            <>
                {/* ONE */}
                <FloatButton type={bookState.repeat.oneToggle ? "primary" : ""}
                             description={"1"}
                             style={{bottom: "7rem"}}
                             onClick={()=>dispatch(bookActions.one())}/>
                {/* A-B */}
                <FloatButton type={bookState.repeat.manyToggle ? "primary" : ""}
                             description={"A-B"}
                             style={{bottom: "4rem"}}
                             onClick={()=>dispatch(bookActions.many())}/>
                <FloatButton
                    shape={"square"}
                    description={<Select style={{width: "4rem"}} defaultValue={bookState.repeat.fromSloka} options={getArray(count)}
                                         onSelect={val => dispatch(bookActions.repeatFrom(val))}/>}
                    style={{width: "4rem", bottom: "4rem", right: "8rem"}}/>
                <FloatButton
                    shape={"square"}
                    description={<Select style={{width: "4rem"}} defaultValue={bookState.repeat.toSloka} options={getArray(count)}
                                         onSelect={val => dispatch(bookActions.repeatTo(val))}/>}
                    style={{width: "4rem", bottom: "4rem", right: "4rem"}}/>
            </>
        }
        <FloatButton shape={"square"}
                     icon={<SyncOutlined/>}
                     description={repeatString}
                     type={repeatString ? "primary" : ""}
                     style={{bottom: "1rem", width: "4rem", height: "2rem"}}
                     onClick={() => setExpand(!expand)}/>
    </>
}
