//
// --------- Content API ---------
//

import axios from "axios"
import yaml from "yaml"

// Text Content
import slokasFile from "../content/gita/slokas/slokas.yaml"
import slokasMeaningFile from "../content/gita/slokas/slokas-meaning.yaml"
import audioTimeFile from "../content/gita/audio/audio-time.yaml"

// Audio Content
import audio1to5 from "../content/gita/audio/chapter-01-05.mp3"
import audio6to12 from "../content/gita/audio/chapter-06-12.mp3"
import audio13to18 from "../content/gita/audio/chapter-13-18.mp3"

// --------------Exported--------------

export let fetcher = {
    slokas: fetchData(slokasFile),
    audioTime: fetchData(audioTimeFile),
    meaning: fetchData(slokasMeaningFile)
}

export let getAudioUrl = chapter => {
    let audioFile;
    switch (true) {
        case [1, 2, 3, 4, 5].includes(chapter):
            audioFile = audio1to5;
            break;
        case [6, 7, 8, 9, 10, 11, 12].includes(chapter):
            audioFile = audio6to12;
            break;
        case [13, 14, 15, 16, 17, 18].includes(chapter):
            audioFile = audio13to18;
            break;
    }
    return audioFile;
}

// --------------Private--------------
async function fetchData(fileName) {
    const response = await axios(fileName);
    let responseObj = yaml.parse(response.data)
    return responseObj;
}
