import {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux"
import {Card, Button} from "antd";

import {bookActions} from "../state/book-reducer"
import * as util from "../utils/util"
import './sloka.css'

export function Sloka(props) {
    let {
        chapNum, slokaNum,
        slokam, meaning,
        audioUrl,
    } = props

    let bookState = useSelector(state => state.book)
    let dispatch = useDispatch()

    const [collapseNum, setCollapseNum] = useState(null);

    useEffect(() => {
        let active = bookState.activeSloka
        if (!active) {
            window.scrollTo(0, 0);
            return
        }

        console.log(active, "C:Sloka useEffect[active]", active)
        // if not seen in screen, scroll..
        let activeElement = document.querySelector("#slokam" + active)
        if (activeElement) {
            if (!util.isInViewport(activeElement)) {
                let prevElement = document.querySelector(active === 1 ? "#bookTitle" : `#slokam${active - 1}`)
                prevElement.scrollIntoView({behavior: "smooth"})
            }
        }
    }, [bookState.activeSloka])

    const toggleSlokaMeaning = e => slokaNumber => {
        e.stopPropagation()
        setCollapseNum(slokaNumber === collapseNum ? 0 : slokaNumber)
    }

    const playPause = slokaClicked => {
        console.log("C:Sloka.playPause(): chap, slokam, audio ", chapNum, slokaNum, audioUrl)

        if (!bookState.activeSloka || bookState.activeSloka !== slokaClicked) {
            dispatch(bookActions.activeSloka(slokaClicked))
        } else {
            song.paused ? song.play() : song.pause()
            return
        }

        // set sloka window & play
        document.querySelector("#songSource").src = audioUrl
        song.currentTime = util.toSeconds(audioUrl.split('#t=')[1])
        song.play()
    }

    return <Card
        id={"slokam" + slokaNum}
        size="small"
        className="sloka-card"
        bodyStyle={{padding: 0}}
    >
        {/*--------- 1. Slokam ---------*/}
        <div className="sloka-lines" onClick={() => playPause(slokaNum)}>
            <Button className="sloka-meaning-button"
                    style={{border: bookState.activeSloka === slokaNum ? "2px solid #906720" : 0}}
                    onClick={e => toggleSlokaMeaning(e)(slokaNum)}>
                {chapNum + "." + slokaNum}
            </Button>
            {slokam.split("\\n").map((line, key) =>
                <span key={key}>
                    {line} <br/>
                </span>
            )}
        </div>
        <div style={{display: collapseNum === slokaNum ? "flex" : "none", padding: "0.6rem"}}>
            <div style={{flexGrow: 1, width: 0}}>
                {/*--------- 2. Word Meanings ---------*/}
                <div className="sloka-meaning">
                    {meaning && meaning.meaning}
                </div>
                {/*--------- 3. Slokam Meaning ---------*/}
                <div className="sloka-translation">
                    {meaning && meaning.translation}
                </div>
            </div>
        </div>
    </Card>;
}