//
// ------------- Audio file, for group of Chapters -------------
//

import {useEffect, useRef} from "react"
import {useDispatch, useSelector} from "react-redux"

import {bookActions} from "../state/book-reducer"

import {toSeconds} from "../utils/util"
import {fetcher, getAudioUrl} from "../content-api/api-util"

export function Audio({chapNum}) {
    let bookState = useSelector(state => state.book)
    let dispatch = useDispatch()

    // for last sloka pause
    useEffect(() => {
        fetcher.audioTime.then(data => {
            slokaRef.current.timeData = data.audio
        })
    }, [])

    const slokaRef = useRef({});

    const getSlokaByCurrentTime = currTime => {
        let slokaTimeMap = Object.entries(slokaRef.current.timeData[chapNum])

        for (let i = 1; i < slokaTimeMap.length; i++) {
            let [aIndex, aStartTime, bIndex, bStartTime] = [...slokaTimeMap[i - 1], ...slokaTimeMap[i]]
            let [a, b] = [toSeconds(aStartTime), toSeconds(bStartTime)]
            aIndex = parseInt(aIndex)
            bIndex = parseInt(bIndex)
            if (a <= currTime && currTime <= b) {
                return aIndex
            } else if (bIndex === slokaTimeMap.length) {
                return bIndex
            }
        }
    }

    // timeupdate: for UI things, i.e scroll to active sloka
    const onTimeUpdateListener = () => {
        // this method CALLED, for approx 4 times in a second
        console.log("monitor-1: ", song.currentTime)

        // Next or Repeat Logic
        let slokaNumFromTime = getSlokaByCurrentTime(song.currentTime)
        let activeRef = bookState.activeSloka
        let timeData = slokaRef.current.timeData
        if (slokaNumFromTime !== activeRef) {
            console.log("monitor-2 (time, slokaFromTime, activeRef): ", song.currentTime, slokaNumFromTime, activeRef)

            // 1. Repeat ONE
            if (bookState.repeat.oneToggle) {
                song.currentTime = toSeconds(timeData[chapNum][activeRef])
                return
            } else if (bookState.repeat.manyToggle) {
                // 2. Repeat Many
                let {fromSloka, toSloka} = bookState.repeat
                if ((slokaNumFromTime < fromSloka || slokaNumFromTime > toSloka) ||  // not in range, from and to
                    slokaNumFromTime - 1 === toSloka) {                              // range last sloka
                    song.currentTime = toSeconds(timeData[chapNum][fromSloka])
                    dispatch(bookActions.activeSloka(fromSloka))
                } else {
                    dispatch(bookActions.activeSloka(slokaNumFromTime))
                }
                return
            }

            // 3. goto NEXT sloka
            dispatch(bookActions.activeSloka(slokaNumFromTime))
        }
        // 4. repeat Last sloka
        if ((bookState.repeat.oneToggle || bookState.repeat.manyToggle) && activeRef === Object.entries(timeData[chapNum]).length) { //
            let temp = song.currentTime
            if(temp > toSeconds(timeData[chapNum][activeRef]) + 15 + 20) {
                if(bookState.repeat.oneToggle){
                    song.currentTime = toSeconds(timeData[chapNum][activeRef])
                }else if(bookState.repeat.manyToggle){
                    song.currentTime = toSeconds(timeData[chapNum][bookState.repeat.fromSloka])
                }
                return
            }
        }
        // 5. PAUSE @ end of chapter
        if (activeRef === Object.entries(timeData[chapNum]).length) {
            let chapEndTime = toSeconds(timeData[chapNum][activeRef]) + 15 + 20; // ex: calculating EOC (end of chap)
            if (song.currentTime > chapEndTime) {       // stop playing @ end of chapter
                song.pause();
                console.log('--( Chapter end )--')
            }
        }
    }

    const url = `${getAudioUrl(chapNum)}`
    return <audio id="song" preload="auto" onTimeUpdate={onTimeUpdateListener}>
        <source id="songSource" src={url} type="audio/mp3"/>
        Your browser does not support the audio element.
    </audio>
}