import React from 'react'
import {Col, Row} from "antd"

import {Sloka} from "./sloka"
import {FloatingMenu} from "./floating-menu"

export function ChapterContent({chapterContent}) {
    if (!chapterContent.slokas) {
        return <></>
    }

    let {
        number,
        name,
        slokas,
        meaning,
        audioTime,
        audioUrl
    } = chapterContent
    let slokaArr = Object.values(slokas)
    let meaningArr = meaning && Object.values(meaning)

    return <>
        <>
            {/*---------------Can be placed anywhere---------------*/}
            <FloatingMenu count={slokaArr.length}/>
        </>
        {/*---------------1. Chapter Title---------------*/}
        <Row className="chapter-title">
            <Col span={24}>
                    <span style={{fontSize: "1.5rem"}}>{name}</span>
            </Col>
        </Row>
        {/*---------------2. Slokam Cards---------------*/}
        <Row style={{textAlign: "left", fontSize: 20, lineHeight: 1.8}}>
            <Col span={24}>
                {slokaArr.map((slokam, i) =>
                    <Sloka
                        key={i + 1}             // react key
                        chapNum={number}
                        slokaNum={i + 1}

                        slokam={slokam}
                        meaning={meaningArr && meaningArr[i]}
                        audioUrl={audioUrl+"#t="+audioTime[i+1]}
                    />
                )}
            </Col>
        </Row>
        {/*---------------3. Chapter Footer---------------*/}
        <Row>
            <Col span={24}>
                <div className="sloka-meaning">
                           <br/>
                      <Footer name={name}/>
                </div>
            </Col>
        </Row>
    </>
}

function Footer({name}) {
    let temp = name.split("")
    return <>
        {temp[1].replace("", "")}
        <p>{temp[0]}</p>
    </>
}