import React, {useState, useEffect} from 'react'
import {useDispatch, useSelector} from "react-redux"
import {Col, Row} from "antd";

import {bookActions, loadContentAsync} from "../state/book-reducer"
import {getAudioUrl} from "../content-api/api-util"
import {ChapterContent} from './chapter-content'
import {Audio} from "./audio"

// ----------------------3. Component: Book----------------------
//
export default function Chapter({chapterNum}) {
    chapterNum = chapterNum || 1;

    const [bookContent, setBookContent] = useState(null);
    let content = useSelector(state => state.content)
    let dispatch = useDispatch()

    useEffect(() => {
        dispatch(loadContentAsync())
    }, []);

    useEffect(() => {
        setBookContent({
            title: content.title,
            slokas: content.slokas,
            meaning: content.meaning,
            audioTime: content.audioTime
        });
    }, [content])

    useEffect(() => {
        dispatch(bookActions.clearRepeatMany())
    }, [chapterNum]);

    return bookContent?.slokas &&
        <>
            <Row>
                <Col>
                    <ChapterContent chapterContent={{
                        number: chapterNum,
                        name: bookContent.title[chapterNum],
                        slokas: bookContent.slokas[chapterNum],
                        meaning: bookContent.meaning[chapterNum],
                        audioTime: bookContent.audioTime[chapterNum],
                        audioUrl: getAudioUrl(chapterNum),
                    }}/>
                    <Audio chapNum={chapterNum}/>
                </Col>
            </Row>
        </>
}
