import {createSlice, configureStore} from '@reduxjs/toolkit'

import {fetcher} from "../content-api/api-util"

// Slice 1, App state-----------------------------
export const bookSlice = createSlice({
    name: 'book',
    initialState: {
        // 1. audio tag
        timeupdateListener: null,
        // 2. repeat button group
        repeat: {
            oneToggle: false,
            manyToggle: false,
            fromSloka: 1,
            toSloka: 5
        },
        // 3.
        activeSloka: 0
    },
    reducers: {
        // 1. Active Sloka..
        "activeSloka": (state, action) => {
            state.activeSloka = action.payload
        },
        // 2. Audio monitor // run listener while playing
        "TimeUpdateListener": (state, action) => {
            if (!state.timeupdateListener) {
                console.log("Reducer(case)TimeUpdateListener",)
                state.timeupdateListener = action.payload
                // song.addEventListener('timeupdate', state.timeupdateListener);
            }
        },
        // 3. get running sloka number -----------not working to look..
        "getSlokaFromCurrentTime": (state, action) => {
            if (!state.getSlokaFromCurrentTime) {
                state.getSlokaFromCurrentTime = action.payload
            }
        },
        // 4. Repeat Cases
        "one": (state, action) => {
            state.repeat.oneToggle = !state.repeat.oneToggle
            state.repeat.manyToggle = false
        },
        "many": (state, action) => {
            state.repeat.manyToggle = !state.repeat.manyToggle
            state.repeat.oneToggle = false
        },
        "repeatFrom": (state, action) => {
            state.repeat.fromSloka = action.payload
        },
        "repeatTo": (state, action) => {
            state.repeat.toSloka = action.payload
        },
        "clearRepeatMany": (state, action) => {
            state.activeSloka = 0
            if (state.repeat.manyToggle) {
                state.repeat.manyToggle = false
                state.repeat.fromSloka = 1
                state.repeat.toSloka = 5
            }
        }
    }
})

export const bookActions = bookSlice.actions

// Slice 2, whole content-----------------------------
export const contentSlice = createSlice({
    name: 'content',
    initialState: {
        title: [],
        slokas: [],
        meaning: [],
        audioTime: []
    },
    reducers: {
        loadContent: (state, action) => {
            state.title = action.payload.title
            state.slokas = action.payload.slokas
            state.meaning = action.payload.meaning
            state.audioTime = action.payload.audioTime
        }
    }
})

export let loadContentAsync = () => async dispatch => {
    let [slokasAndTitles, meaning, audioTimeline] = await Promise.all(
        [fetcher.slokas, fetcher.meaning, fetcher.audioTime]
    )

    dispatch(contentSlice.actions.loadContent({
        title: slokasAndTitles.title,
        slokas: slokasAndTitles.slokas,
        meaning: meaning.slokasMeaning,
        audioTime: audioTimeline.audio
    }))
}

// Rule - Single store for Redux-----------------------------
export const bookStore = configureStore({
    reducer: {
        book: bookSlice.reducer,
        content: contentSlice.reducer
    }
})
